import axios from 'axios'
export let isDev = false
import router from '../router'
import el from 'element-ui'
import qs from 'qs'
export const baseURL = isDev ? "http://rap2.taobao.org:38080/app/mock/245259" : "http://ruankun.xyz:8821/disease/"
const service = axios.create({
    baseURL,
})
service.interceptors.request.use((config) => {
    config.headers = Object.assign({}, config.headers, {
        token: sessionStorage.getItem('token')
    })
    if (config.url === '/laboratory' && config.method === 'post') {
        config.headers['Content-Type'] = 'application/json;charset=UTF-8'
    } else {
        config.data = qs.stringify(config.data)
    }
    return config
})
service.interceptors.response.use((res) => {
    //
    let data = isDev ? res : res.data
    const SUCCESS_STATUS = isDev ? 200 : 1
    if (data.status === SUCCESS_STATUS) {
        return res.data
    } else {
        // errorHandle()
        return res
    }
})
export const Service = service
export const errorHandle = () => {
    checkToken()
        .then(res => {
            // console.log(res);
            if (res.data.status == -1) {
                el.MessageBox({
                    message: ",!",
                    confirmButtonText: "",
                    callback: action => {
                        router.push("/login");
                    }
                });
            }
        })
        .catch(() => {
            // console.log(router)
            if (router.history.current.path.indexOf('/manager') !== -1) {
                // console.log(111)
                // router.push('500')
            } else {
                el.Message({
                    message:",",
                    type:"error"
                })
            }
        });
}

